.main-my {
    @include extensive-content;
}

.ul-title-bar {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
    border-bottom: 2px solid $border-color;
    padding: 15px 0 15px;

    > li {
        width: 100%;
        text-align: center;
        font-size: 16px;
        cursor: pointer;
    }

    > .list-title.on {
        position: relative;
        font-weight: bold;

        &::after {
            position: absolute;
            content: '';
            width: calc(100% - 25px);
            bottom: -18px;
            left: 50%;
            border: 2px solid $active-color;
            -webkit-transform: translate(-50%, 0);
            -ms-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
        }
    }
}

.ul-summary-community {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    border-bottom: 2px solid $border-color;
    padding: 15px 0 15px;

    > li {
        width: 100%;
        text-align: center;
        font-size: 16px;
        cursor: pointer;

        > p {
            margin: 0;
        }
    }

    > .list-summary-community.on {
        position: relative;
        font-weight: bold;

        &::after {
            position: absolute;
            content: '';
            width: calc(100% - 25px);
            bottom: -18px;
            left: 50%;
            border: 2px solid $active-color;
            -webkit-transform: translate(-50%, 0);
            -ms-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
        }
    }
}

// 프로필
.ul-profile {
    > li {
        border-bottom: 1px solid $border-color;
    }
}

.style-wrapper-profile {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: $padding;
    gap: 15px;
}

// 프로필 사진
.wrapper-profile-image {
    position: relative;
    width: 70px;
    height: 70px;
}

// 프로필 사진 우측
.wrapper-id-bookmark {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    gap: 15px;

    .container-id-nick {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: end;
        -ms-flex-align: end;
        align-items: flex-end;
        gap: 10px;

        .txt-profile-userId {
            font-size: 18px;
            font-weight: bold;
        }

        .txt-profile-nickName {
            font-size: 16px;
        }
    }

    .container-profile-bookmark-like {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;

        .num-profile-bookmark {
            margin-right: 5px;
        }
    }
}

.container-notice.profile {
    font-size: 13px;
    margin: 20px 20px 0;
}

.container-profile-img {
    width: 70px;
    height: 70px;

    > img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        border: 1px solid $border-color;
    }
}

.form-edit-nickName {
    position: relative;
}

.num-count-nickName {
    position: absolute;
    margin: 0;
    top: 50%;
    right: 7px;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    font-size: 13px;
    text-align: right;
}

#editNickNameInput {
    @include styled-input;
    width: 100%;
    height: auto;
    padding: 10px;
}

.container-notice.edit-nickName {
    border: 0;
    padding: 0;
    margin: 10px 0;

    > em {
        margin: 0;
        font-size: 13px;
    }
}

